import * as echarts from "echarts/core";
import {
    TitleComponent,
    TitleComponentOption,
    TooltipComponent,
    TooltipComponentOption,
} from "echarts/components";
import { PieChart, PieSeriesOption } from "echarts/charts";
import { LabelLayout } from "echarts/features";
import { CanvasRenderer } from "echarts/renderers";
import { DeckTypeImageSize, MonthlyDecks } from "../common/deck";
import qs from 'query-string';

echarts.use([
    TitleComponent,
    TooltipComponent,
    PieChart,
    CanvasRenderer,
    LabelLayout,
]);

type EChartsOption = echarts.ComposeOption<
    TitleComponentOption | TooltipComponentOption | PieSeriesOption
>;

const LAST_PERCENT_AGGS = 0.1;

var chartDom = document.getElementById("main")!;

var myChart = echarts.init(chartDom);

interface Point2d {
    x: number;
    y: number;
}

const center: Point2d = {
    x: chartDom.offsetWidth / 2,
    y: chartDom.offsetHeight / 2,
};

const radius = Math.min(center.x, center.y) * 0.8;

function transformDeckTypeData(deckData: MonthlyDecks) {
    const { deckTypeImage, decks } = deckData;
    const map: Record<string, number> = {};
    decks.forEach((deck) => {
        if (!map[deck.deckType.name]) {
            map[deck.deckType.name] = 0;
        }
        map[deck.deckType.name] += 1;
    });

    let data = Object.entries(map).sort((a, b) => b[1] - a[1]).map(([name, value]) => {
        return { name, value };
    });

    const total = data.reduce((acc, cur) => {
        return acc + cur.value;
    }, 0);

    let i = data.length - 1, acc = 0;
    for (; acc / total < LAST_PERCENT_AGGS && i >= 0; i--) {
        acc += data[i].value;
    }
    data = data.slice(0, i + 1);
    data.push({ name: 'Other', value: acc });
    
    const startRadians = data.reduce((acc, cur) => {
        acc.push(acc[acc.length - 1] + cur.value / total * Math.PI * 2);
        return acc;
    }, [0]);

    const deckImgMap = new Map(deckTypeImage);
    deckImgMap.set('Other', 'https://images.weserv.nl/?url=https://s3.duellinksmeta.com/cards/60c2b3aca0e24f2d54a5314b_w420.webp&cx=50&cy=115&cw=320&ch=320&w=420&we&precrop=true&output=webp&maxage=7d');

    return data.map((d, i) => {
        const aabb = getImageAABBOfPie(center, radius, startRadians[i], startRadians[i + 1]);
        const scale = Math.max(aabb.width, aabb.height) / DeckTypeImageSize;
        const image = new Image();
        image.src = deckImgMap.get(d.name)!;
        return {
            name: d.name,
            value: d.value,
            itemStyle: {
                color: {
                    image,
                    repeat: "no-repeat",
                    x: aabb.min.x / scale,
                    y: aabb.min.y / scale,
                    scaleX: scale,
                    scaleY: scale,
                }
            }
        }
    })
}

/**
 * 计算pie的轴对称包围盒
 */
function getAABBOfPie(center: Point2d, radius: number, startRadian: number, endRadian: number) {
    // clockwise

    const startPoint = {
        x: radius * Math.sin(startRadian) + center.x,
        y: -radius * Math.cos(startRadian) + center.y,
    };
    const endPoint = {
        x: radius * Math.sin(endRadian) + center.x,
        y: -radius * Math.cos(endRadian) + center.y,
    };

    const min = {
        x: center.x - radius,
        y: center.y - radius,
    };
    const max = {
        x: center.x + radius,
        y: center.y + radius,
    };

    const step = [0, Math.PI / 2, Math.PI, Math.PI * 3 / 2];
    if (step[1] < startRadian || step[1] > endRadian) {
        max.x = Math.max(startPoint.x, endPoint.x, center.x);
    }

    if (step[2] < startRadian || step[2] > endRadian) {
        max.y = Math.max(startPoint.y, endPoint.y, center.y);
    }

    if (step[3] < startRadian || step[3] > endRadian) {
        min.x = Math.min(startPoint.x, endPoint.x, center.x);
    }

    // 0 的情况一定要计算
    min.y = Math.min(startPoint.y, endPoint.y, center.y);

    return {
        min, max,
        center: {
            x: (min.x + max.x) / 2,
            y: (min.y + max.y) / 2,
        },
        width: max.x - min.x,
        height: max.y - min.y,
    };
}

/**
 * 计算pie的中心
 */
function getCenterOfPie(center: Point2d, radius: number, startRadian: number, endRadian: number) {
    const radian = (startRadian + endRadian) / 2;
    return {
        x: radius / 2 * Math.sin(radian) + center.x,
        y: -radius / 2 * Math.cos(radian) + center.y,
    }
}

function getImageAABBOfPie(center: Point2d, radius: number, startRadian: number, endRadian: number) {
    const aabb = getAABBOfPie(center, radius, startRadian, endRadian);
    const c = getCenterOfPie(center, radius, startRadian, endRadian);
    const width = Math.max(c.x - aabb.min.x, aabb.max.x - c.x) * 2;
    const height = Math.max(c.y - aabb.min.y, aabb.max.y - c.y) * 2;
    return {
        min: { x: c.x - width / 2, y: c.y - height / 2 },
        max: { x: c.x + width / 2, y: c.y + height / 2 },
        center: c,
        width,
        height,
    };
}

interface PieDataItem {
    name: string;
    value: number;
}

function updateChart(data: PieDataItem[]) {
    const option: EChartsOption = {
        tooltip: {},
        series: [
            {
                type: "pie",
                width: chartDom.offsetWidth,
                height: chartDom.offsetHeight,
                selectedMode: "single",
                selectedOffset: 30,
                clockwise: true,
                radius: "80%",
                label: {
                    show: true,
                    fontWeight: 'bold',
                    formatter: '{b}\n{d}%'
                },
                labelLayout: {
                    hideOverlap: false,
                },
                data,
            },
        ],
    };
    myChart.setOption(option);
}

async function main() {
    myChart.showLoading();
    try {
        const cur = new Date();
        const date = qs.parse(location.search).date || `${cur.getFullYear()}-${cur.getUTCMonth() + 1}`;
        const data: MonthlyDecks = await (await fetch(`https://cdn.jsdelivr.net/gh/Frezc/dldata/${date}.json`)).json();
        updateChart(transformDeckTypeData(data));
    } catch (e) {
        alert(`data load error: ${e}`);
    } finally {
        myChart.hideLoading();
    }

}

main();
